@import "../../colors.scss";

.iconButtonText {
    color: #21c0fc;
    text-decoration: none;
    margin-top: -15px;
    font-size: 45px;
}

.iconButton-white {
    .iconButtonText {
        color: #ffffff;
    }
}

.disabled .iconButtonText {
    color: $grey;
}

.iconButton {
  background: transparent no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  margin-left: auto;
  margin-right: auto;
}
